import uniIcon from '@/componets/uni/rate/uni-icon.vue'
export default {
  name: 'activityTicketBottom',
  props: {
    assist: Object,
    isFavorites: Number,
	isShow: {
		type: Boolean,
		default: true
	}
  },
  components: {
	  uniIcon
  },
  data () {
    return {
      collectionState: this.isFavorites
    }
  },
  watch: {
    isFavorites (newVal, oldVal) {
      this.collectionState = newVal
    }
  },
  computed: {
    buttonName () {
      if (this.$userInfo.isLogin()) {
        if (this.assist && this.assist.join.status === 1) {
          return '立即购买'
        } else {
          return '邀请好友'
        }
      } else {
        return '邀请好友'
      }
    }
  },
  methods: {
    collection () {
      this.$emit('collection')
    },
    seeOtherGoods () {
      this.$emit('seeOtherGoods')
    },
    invoteFriend () {
      this.$emit('invoteFriend')
    }
  }
}
